* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.task-container {
  width: 70%;
  margin: 50px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 32px;
}

.taskItem {
  border: 2px solid #22677c;
  padding: 10px;
  border-radius: 10px;
}

.des-container {
  margin-top: 8px;
  border-top: 2px solid #22677c;
}

.title,
.des {
  font-weight: bold;
}

.btn-container {
  display: flex;
  justify-content: center;
}

.btn {
  display: inline-block;
  background-color: #5ec576;
  font-size: 16px;
  font-weight: 500;
  border: none;
  padding: 5px 10px;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s;
}

.btn:hover {
  background-color: #4bbb7d;
}
